/// Buttons

.c-btn {
  @include btn-base;

  &:active,
  &-active {
    @include btn-active;
  }

  &-primary {
    @include btn-style($btn-primary-bg);

    @if $btn-white-label == false {
      &:hover {
        background-color: $btn-primary-hover;
        background-image: none;
      }
    }

    &-outline {
      @include btn-outline($btn-primary-bg);
    }
  }

  &-secondary,
  &-default {
    @include btn-secondary($btn-secondary-bg);

    &-outline {
      @include btn-outline($color-gray-7);
    }
  }

  &-success {
    @include btn-style($btn-success-bg);

    @if $btn-white-label == false {
      &:hover {
        background-color: $btn-success-hover;
        background-image: none;
      }
    }

    &-outline {
      @include btn-outline($btn-success-bg);
    }
  }

  &-danger {
    @include btn-style($btn-danger-bg);

    @if $btn-white-label == false {
      &:hover {
        background-color: $btn-danger-hover;
        background-image: none;
      }
    }

    &-outline {
      @include btn-outline($btn-danger-bg);
    }
  }

  @if $btn-active-selected {
    @include btn-active-selected;
  }

  &-table-icon {
    @include btn-table-icon;
  }
}

.c-btn-link {
  @include btn-base;

  color: $primary;
  box-shadow: none;

  &:hover {
    color: lighten($primary, 10);
  }

  &:active {
    color: darken($primary, 10);
    box-shadow: none;
  }

  &-disabled {
    &,
    &:hover,
    &:focus,
    &:active {
      background: transparent;
      color: $btn-disabled-c;
      line-height: 1;
      cursor: not-allowed;
      appearance: none;
      box-shadow: none;
      opacity: $btn-disabled-opacity;
    }
  }
}


.c-btn,
.c-btn-link {
  &-xs {
    @include btn-size(xs);
  }

  &-sm {
    @include btn-size(sm);
  }

  &-lg {
    @include btn-size(lg);
  }

  &-xl {
    @include btn-size(xl);
  }
}

.c-btn-block {
  display: block;
  width: 100%;
  text-align: center;
}

.c-btn-disabled,
.c-btn[disabled] {
  @include btn-disabled;
}

//TODO cleaner approach
.c-btn-secondary.c-btn-active {
  box-shadow: $btn-shadow-active;
}

.c-btn-link {
  &-secondary,
  &-default {
    @include btn-link-style($color-gray-7);
  }

  &-success {
    @include btn-link-style($success);
  }

  &-danger {
    @include btn-link-style($danger);
  }

  &[disabled] {
    opacity: 0.8;
    color: $btn-disabled-c;
  }
}
