////
/// @group breakpoints
////

// $breakpoints
// $breakpoints: (
//   xs: 0,
//   sm: 576px,
//   md: 768px,
//   lg: 992px,
//   xl: 1200px,
//   xxl: 1440px) !default;
@import "../grid/_variables.scss";

// $show-breakpoints: "true";
$bp-show-breakpoints: (xs, sm, md, lg, xl);
$bp-media-type: all !default;
// Get a breakpoint's width
//  $tablet-width: bp-get-breakpoint-width(tablet);
//  @media (min-width: bp-get-breakpoint-width(desktop)) {}
@function bp-get-breakpoint-width($name, $breakpoint: $breakpoints) {
  @if map-has-key($breakpoint, $name) {
    @return map-get($breakpoint, $name);
  }

  @else {
    @warn "Breakpoint #{$name} wasn't found in $breakpoint.";
  }
}

@mixin bp($from: false, $until: false, $and: false, $media-type: $bp-media-type, $breakpoint: $breakpoints) {
  $min-width: 0;
  $max-width: 0;
  $media-query: '';
  // From: this breakpoint (inclusive)
  @if $from {
    @if type-of($from) == number {
      $min-width: $from;
    }

    @else {
      $min-width: bp-get-breakpoint-width($from, $breakpoint);
    }
  }

  // Until: that breakpoint (exclusive)
  @if $until {
    @if type-of($until) == number {
      $max-width: $until;
    }

    @else {
      $max-width: bp-get-breakpoint-width($until, $breakpoint) - 2;
    }
  }

  @if $min-width != 0 {
    $media-query: '#{$media-query} and (min-width: #{$min-width})';
  }

  @if $max-width != 0 {
    $media-query: '#{$media-query} and (max-width: #{$max-width})';
  }

  @if $and {
    $media-query: '#{$media-query} and #{$and}';
  }
  // Remove unnecessary media query prefix 'all and '
  @if ($media-type == 'all' and $media-query != '') {
    $media-type: '';
    $media-query: str-slice(unquote($media-query), 6);
  }

  @media #{$media-type + $media-query} {
    @content;
  }
}

//  @include bp-show-breakpoints;
// @include bp-show-breakpoints((L, XL), (S: 300px, L: 800px, XL: 1200px));
@mixin bp-show-breakpoints($show-breakpoints: $bp-show-breakpoints, $breakpoint: $breakpoints) {
  body::before {
    background-color: lighten(#0082de, 60%);
    border-bottom: 1px solid lighten(#0082de, 50%);
    border-left: 1px solid lighten(#0082de, 50%);
    color: #0082de;
    font: small-caption;
    padding: 3px 6px;
    pointer-events: none;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;

    @each $show-breakpoint in $show-breakpoints {
      $width: bp-get-breakpoint-width($show-breakpoint, $breakpoint);

      @include bp($show-breakpoint, $breakpoint: $breakpoint) {
        content: "#{$show-breakpoint}  #{$width} (#{$width)})";
      }
    }
  }
}

