$main-content-padding: $gutter !default;

.c-wrapper {
  display: flex;
  min-height: 100vh;
  width: 100%;
}

.c-main-content {
  background-color: $white;
  color: $black;
  flex-direction: row;
  flex: 1;
  padding: $main-content-padding;

  @media (min-width: 480px) and (max-width: 1024px) {
    padding: 20px;
  }
}
