.c-avatar {
  position: relative;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  color: #fefefe;
  background: #f1f3f5;
  border-radius: 50%;
  text-align: center;
  white-space: nowrap;
  font-weight: bold;
  font-size: 12px;
  vertical-align: middle;
  margin: 0;
  box-shadow: 1px 2px 5px 1px rgba(0, 0, 0, 0.06);
}

.c-avatar[data-text]::after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.c-avatar img {
  width: 100%;
  border-radius: 50%;
}

.c-avatar .fa {
  font-size: 16px;
  line-height: 2.5rem;
}

.c-avatar[data-status]::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  margin: 1px;
  width: 8px;
  height: 8px;
  background-clip: padding-box;
  border-radius: 50px;
  box-shadow: 0 0 0 0.1rem #fefefe;
  color: #fefefe;
}

.c-avatar[data-status='primary']::before {
  background-color: #0078e0;
}

.c-avatar[data-status='success']::before {
  background-color: #37b24d;
}

.c-avatar[data-status='warning']::before {
  background-color: #f59f00;
}

.c-avatar[data-status='danger']::before {
  background-color: #f03e3e;
